





























































































































































































































































    export default {
        data() {
            return {
				order_noSearch:'',
				customer_nameSearch:'',
                uid: 0,
                list: [],
                clientId: 0,
                clientName: "",
                showClient: false,
                clientList: [],
                showGood: false,
                goodList: [],
                textGood: "",
                itemIndex: 0,
                preventEvent: false,
				page:1,
				pageShop:1,
				pageexpress:1,
				total:'',
				totalShop:'',
				username:'',
				
				date: "",//日期
				order_no:'',//单号
				checkedArr:[], //复选框选中的值，工种
				allCheck : {
					name : '全选',
					value : 'all',
					checked : false	
				},
				invoiceList: [
					{
						id:0,
						name:'否'
					},
					{
						id:1,
						name:'是'
					},
				],
				express_no:'',//快递单号
				express_code:'',//快递编号
				express_name:'',//快递公司
				showExpress:false,
				ExpressList:[],
				expressSearch:'',
				totalexpress:'',
            }
        },
        mounted() {
        },
        onLoad() {
			let dateTime = new Date().toISOString()
			this.date = this.dateFormat(dateTime)
            this.uid = this.common.get("uid")
        },
        methods: {
			// 优先补发扣除
			changeinvoice(eve,i) {
				this.list[i].wIndex = eve.detail.value
				this.list[i].is_supply = this.invoiceList[this.list[i].wIndex].id
			},
			changeDate(eve) {
				this.date = eve
			},
			// 全选事件
			changeAll(e){
				console.log('changeAll',e)
			    let chooseItem = e.detail.value;
			    // 全选
			    if(chooseItem[0]=='all'){
			        this.allCheck.checked=true;
			        for(let item of this.goodList){
			            let itemVal=String(item.sales_detail_id);
			            if(!this.checkedArr.includes(itemVal)){
			                this.checkedArr.push(itemVal);
						}
			        }
			    }else{
			        // 取消全选
			        this.allCheck.checked=false;
			        this.checkedArr=[];
			    }
			},
			// 多选复选框改变事件
			changeCheck(e){
				this.checkedArr = e.detail.value;
				console.log('this.checkedArr',this.checkedArr)
				// 如果选择的数组中有值，并且长度等于列表的长度，就是全选
				if(this.checkedArr.length>0 && this.checkedArr.length==this.goodList.length){
				    this.allCheck.checked=true;
				}else{
				    this.allCheck.checked=false;
				}
			},
			noneEnoughPeople() {
				
			},
			dateFormat(time) {
			    let date = new Date(time);
			    let year = date.getFullYear();
			    // 在日期格式中，月份是从0开始的，因此要加0，使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
			    let month = date.getMonth() + 1 < 10 ? "0" + (date.getMonth() + 1) : date.getMonth() + 1;
			    let day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
			    let hours = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
			    let minutes = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
			    let seconds = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
			    // 拼接
			    return year + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds;
			    // return year + "-" + month + "-" + day;
			},
			
			// 销售单列表
			clientInList() {
				this.http({
				    url: this.api.Returns_sales_list,
				    method: "POST",
				    data: {
				        uid: this.common.get("uid"),
						customer_name:this.customer_nameSearch,
						order_no:this.order_noSearch,
						page:this.page-1,
						length:20
				    },
				    success: res => {
						this.clientList= res.list
						this.total = res.total
				    }
				})
			},
			// 销售单选择点击
			selectClient(i) {
				this.clientId = this.clientList[i].id
				this.clientName = this.clientList[i].order_no
				this.showClient = false
			    this.preventEvent = false
				this.page = 1
			},
			selectExpress(i) {
				this.express_name = this.ExpressList[i].name
				this.express_code = this.ExpressList[i].comCode
				this.showExpress = false
				this.preventEvent = false
				this.pageexpress = 1
			},
			// 销售单搜索
			searchClient() {
			    this.clientInList()
			},
			handlePage(params) {
				this.page = params.current;
				this.clientInList()
			},
			handlePageShop(params) {
				this.pageShop = params.current;
				this.shopList()
			},
			handlePageExpress(params) {
				this.pageexpress = params.current;
				this.expressList()
			},
			// 商品接口
			shopList() {
				this.http({
				    url: this.api.Returns_sales_goods_list,
				    method: "POST",
				    data: {
				        uid: this.uid,
						sales_id: this.clientId,
						page:this.pageShop-1,
						length:20
				    },
				    success: res => {
				        this.goodList = res.list
						this.totalShop = res.total
				    }
				})
			},
			// 商品搜索
			searchGood(index) {
				this.shopList()
			},
			// 名称选择
            openModal(name) {
                this.preventEvent = true
                if (name == "client") {
                    this.showClient = true
					this.clientInList()
                } else if(name == 'good') {
					if(this.clientId == '') {
						uni.showToast({
							title:'请先选择销售单',
							icon:'none',
							duration: 2000
						})
					} else {
						this.showGood = true
						this.shopList()
					}
				} else {
					this.showExpress = true
					this.expressList()
				}
            },
			// 快递搜索
			searchexpress() {
				this.expressList()
			},
			expressList() {
				this.http({
				    url: this.api.Common_express_list,
				    method: "POST",
				    data: {
				        uid: this.uid,
						name: this.expressSearch,
						page:this.pageexpress-1,
						length:20
				    },
				    success: res => {
				        this.ExpressList = res.list
						this.totalexpress = res.total
				    }
				})
			},
			// 弹窗关闭按钮
            closeModal(name) {
                this.preventEvent = false
                if (name == "client") {
                    this.showClient = false
                } else if (name == "good") {
                    this.showGood = false
                }
				this.page = 1
				this.pageShop = 1
				this.checkedArr = []
            },
			
			// 商品选择点击
            selectGood() {
                // this.prevent = false
                // this.showGood = false
				this.http({
				    url: this.api.Returns_get_sales_goods_by_id,
				    method: "POST",
				    data: {
				        uid: this.uid,
						id:this.checkedArr,
				    },
				    success: res => {
						res.map((item,index) => {
							item.wIndex = -1
							this.list.push(item)
						})
				    }
				})
				console.log('this.list',this.list)
            },
            close() {
                this.preventEvent = false
            },
			// 商品删除按钮
            del(index) {
				this.list.splice(index, 1)
            },
			// 销售数量  总价
            num(e, i) {
                const a = e.target.value
                const inputRule = /[^\d]/g
				this.list[i].countReturn = a.replace(inputRule,'')
            },
			// 快递单号识别
			express(e) {
				this.express_no = e.detail.value
				this.http({
				    url: this.api.Common_auto_express,
				    method: "POST",
				    data: {
				        uid: this.uid,
						express_no:e.detail.value,
				    },
				    success: res => {
						this.express_name = res.name
						this.express_code = res.comCode
				    }
				})
			},
            updatePriceArr() {
                let arr = []
                for (let i = 0; i < this.list.length; i++) {
                    if (this.list[i].id != 0) {
                        arr.push(this.list[i])
                    }
                }
                return arr
            },
			// 返回上一页
			previous() {
				let pages = getCurrentPages(); // 当前页面
				let beforePage = pages[pages.length - 2]; // 上一页
				uni.navigateBack({
				    success: function() {
				        beforePage.onLoad(); // 执行上一页的onLoad方法
				    }
				});
			},
			// 确定添加按钮点击
            submit() {
				let a = []
				this.list.map((item,index) =>{
					a.push({'sales_detail_id':item.id ,'count':item.countReturn,'remark':item.remark,'is_supply':item. is_supply})
				})
				uni.showModal({
					title: '提示',
					content: '确定当前操作吗？',
					success: res => {
						if(res.confirm) { 
							this.http({
							    url: this.api.Returns_submit,
							    method: "POST",
							    data: {
							        uid: this.uid,
									date: this.date,//日期
									order_no:this.order_no,//单号
									sales_id:this.clientId,//客户id
									express_no:this.express_no,//快递单号
									express_code:this.express_code,//快递编号
									express_name:this.express_name,//快递公司
									total_data:JSON.stringify(a),
							    },
							    success: res => {
									uni.showToast({
										title:'操作成功',
										icon:'none',
										duration: 2000
									})
									setTimeout(() =>{
										this.previous()
									},500) 
							    }
							})
						}
					},
					fail: () => {},
					complete: () => {}
				});			
            }
        }
    }
